// jshint unused: true, undef:true

define(['jquery', 'core/log'], function($, log) {

    var mplayer = {

        init: function(params) {
            var playerid = 'flp' + params['id'];
            $('#' + playerid).css('width', params['width']);
            $('#' + playerid).css('height', params['height']);

            log.debug("AMD Mplayer sizes initialized");
        }
    };

    return mplayer;

});
